.navbar-nav {
  padding-left: 20px;
  border-left: 1px solid #515151;

  .nav-item {
    margin: 0;

    .nav-link {
      text-transform: capitalize;
      color: $text-color;
      padding: 5px 0;
      font-size: 16px;
      font-family: $secondary-font;
      transition: .2s ease;

      &.active {
        color: $white;
      }
    }
  }
}

.navbar-toggler {
  display: none;

  @include tablet {
    display: block;
  }
}

.main-wrapper {
  display: flex;

  @include tablet {
    overflow-x: hidden;
  }
}

.wrapper {
  width: calc(100% - 340px);
  transition: .2s ease;
  position: relative;

  &-bg {
    position: fixed;
    z-index: -1;
    top: 0;
    right: 0;
    max-width: 60%;
  }

  @include desktop-lg {
    width: calc(100% - 250px);
  }

  @include tablet {
    width: 100%;
    padding-top: 40px;
  }

  &.hide {
    transform: translateX(250px);
  }
}

.sidenav {
  position: sticky;
  top: 0;
  z-index: 9;
  min-width: 340px;
  min-height: 100vh;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: .2s ease;

  @include desktop-lg {
    min-width: 250px;
    padding: 60px 20px;
  }

  @include tablet {
    position: fixed;
    padding: 40px 20px;
    background-color: $body-color;
    left: -300px;
  }

  &.show {
    @include tablet {
      left: 0;
    }
  }
}

.video {
  position: relative;

  img {
    border-radius: 15px;
    width: 100%;
  }

  &-btn {
    height: 45px;
    width: 45px;
    line-height: 45px;
    background-color: $primary-color;
    border-radius: 50%;
    display: block;
    position: absolute;
    text-align: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: $white;
    box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.2);
    transition: .2s ease;

    &:hover {
      box-shadow: 0 0 0 15px rgba(255, 255, 255, 0.3);
      color: $white;
      text-decoration: none;
    }
  }
}

// block
.block {
  background-color: $white;
  border-radius: 30px;
  padding: 40px;
  margin: 30px;
  box-shadow: 0px 0px 20px 5px rgba(61, 61, 61, 0.11);

  &-title {
    margin-bottom: 30px;
    text-align: center;
  }
}

// default slider
.logo-slider {
  .slick-track {
    display: flex;
    align-items: center;

    a {
      display: block;
      text-align: center;
      padding: 10px 25px;

      img {
        display: initial;
        max-width: 100%;
        height: auto;
      }
    }
  }

  .slick-dots {
    padding: 0;
    text-align: center;

    li {
      display: inline-block;
      margin: 5px;

      &.slick-active {
        button {
          background-color: $primary-color;
          border-color: $primary-color;
        }
      }

      button {
        color: transparent;
        padding: 0;
        background-color: transparent;
        border: 1px solid $border-color;
        height: 10px;
        width: 10px;
        border-radius: 50%;

        &:focus {
          outline: 0;
        }
      }
    }
  }

  .slick-arrow {
    position: absolute;
    background-color: transparent;
    border: 1px solid $border-color;
    border-radius: 30px;
    height: 25px;
    line-height: 25px;
    width: 40px;
    bottom: -30px;
    transition: .2s ease;
    font-size: 10px;

    &:focus {
      outline: 0;
    }

    &.arrow-prev {
      left: calc(50% - 50px);
    }

    &.arrow-next {
      right: calc(50% - 50px);
    }

    &:hover {
      color: $white;
      background-color: $primary-color;
      border-color: $primary-color;
    }
  }
}


// logo slider 2
.logo-slider-2 {
  margin-bottom: 30px;
}


// logo slider 3
.slider-thumb {
  .slick-dots li {
    transition: .3s ease;

    img {
      height: 15px;
      width: 15px;
      border-radius: 50%;
      cursor: pointer;
      object-fit: cover;
      transition: inherit;
    }
  }

  .slick-dots li.slick-active img {
    border: 1px solid $primary-color;
    transform: scale(1.1);
  }
}

// logo slider 4
.slider-grayscale {
  img {
    filter: grayscale(100%);
    transition: .2s ease;

    &:hover {
      filter: grayscale(0%);
    }
  }
}

// logo slider 5
.slider-centermode {
  .slick-slide {
    transition: .2s linear all;
    transform: scale(0);

    &.slick-active {
      transform: scale(.7);

      &.slick-center {
        transform: scale(1);
        margin: 0 20px;
      }
    }
  }
}

// logo slider 7
.slider-boxy{
  .slick-slide {
    height: 100px;
    width: 200px;
    background-color: $white;
    border-radius: 15px;
    padding: 10px;
    margin: 10px;
    display: flex !important;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,.07);
    transition: .2s ease;
    &:hover{
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,.11);
    }
    img{
      max-height: 100%;
    }
  }
}




// logo slider 8
.slider-border{
  .slick-slide {
    height: 100px;
    width: 200px;
    padding: 10px;
    margin: 10px;
    display: flex !important;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 3px solid transparent;
    position: relative;
    img {
      transform: translate3d(0, 0.7em, 0);
      display: block;
      max-height: 100%;
      transition: transform 0.4s cubic-bezier(0.2, 0, 0, 1) 0.4s;
    }
    &::after {
      position: absolute;
      content: '';
      bottom: -3px;
      left: calc(0.7em * 1.2);
      right: calc(0.7em * 1.2);
      height: 3px;
      background: $primary-color;
      transition: transform 0.8s cubic-bezier(1, 0, 0.37, 1) 0.2s, right 0.2s cubic-bezier(0.04, 0.48, 0, 1) 0.6s, left 0.4s cubic-bezier(0.04, 0.48, 0, 1) 0.6s;
      transform-origin: left;
    }
    &:hover,&:active{
      &:after{
        transform: scale3d(0, 1, 1);
        right: -3px;
        left: -3px;
        transform-origin: right;
        transition: transform 0.2s cubic-bezier(1, 0, 0.65, 1.01) 0.17s, right 0.2s cubic-bezier(1, 0, 0.65, 1.01), left 0s 0.3s;
      }
      img{
        transform: translate3d(0, 0, 0);
        transition: transform 0.6s cubic-bezier(0.2, 0, 0, 1) 0.4s;
      }
      .line{
        transform: scale3d(1, 1, 1);
        &.right {
          transition: transform 0.1s cubic-bezier(1, 0, 0.65, 1.01) 0.2s;
          transform-origin: bottom;
        }
        &.top {
          transition: transform 0.08s linear 0.4s;
          transform-origin: right;
        }
        &.left {
          transition: transform 0.08s linear 0.48s;
          transform-origin: top;
        }
        &.bottom {
          transition: transform 0.5s cubic-bezier(0, 0.53, 0.29, 1) 0.56s;
          transform-origin: left;
        }
      }
    }
  }
  .line {
    position: absolute;
    background: $primary-color;
    &.right,&.left {
      width: 3px;
      bottom: -3px;
      top: -3px;
      transform: scale3d(1, 0, 1);
    }
    &.top,&.bottom{
      height: 3px;
      left: -3px;
      right: -3px;
      transform: scale3d(0, 1, 1);
    }
    &.right {
      right: -3px;
      transition: transform 0.1s cubic-bezier(1, 0, 0.65, 1.01) 0.23s;
      transform-origin: top;
    }
    &.top {
      top: -3px;
      transition: transform 0.08s linear 0.43s;
      transform-origin: left;
    }
    &.left {
      left: -3px;
      transition: transform 0.08s linear 0.51s;
      transform-origin: bottom;
    }
    &.bottom {
      bottom: -3px;
      transition: transform 0.3s cubic-bezier(1, 0, 0.65, 1.01);
      transform-origin: right;
    }
  }
}